fieldset {
    border: 1px solid silver;
    margin: 0 2px;
    padding: .35em .625em .75em 
}
legend {
    box-sizing: border-box;
    color: inherit;
    display: table;
    max-width: 100%;
    padding: 0;
    white-space: normal 
}
textarea {
    overflow: auto 
}
[type=checkbox], [type=radio] {
    box-sizing: border-box;
    padding: 0 
}
[type=number]::-webkit-inner-spin-button, [type=number]::-webkit-outer-spin-button {
    height: auto 
}
[type=search] {
    -webkit-appearance: textfield;
    outline-offset: -2px 
}
[type=search]::-webkit-search-cancel-button, [type=search]::-webkit-search-decoration {
    -webkit-appearance: none 
}
::-webkit-input-placeholder {
    color: inherit;
    opacity: .54 
}
::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit 
}
.h-wrapper {
    height: 100vh;
    position: relative 
}
.h-wrapper-50 {
    height: 60vh;
    position: relative 
}
.child {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 1 
}
.header {
    padding: 16px 
}
.logo {
    padding: 12px 0;
    margin: 0 
}
@media screen and (max-width:780px) {
    .h-wrapper-50 {
        height: 100vh;
        position: relative 
   }
    .logo {
        position: absolute;
        padding: 1.2rem 0;
        left: 50%;
        transform: translateX(-66px) 
   }
    .header {
        padding: 0 
   }
}
.nav {
    font-size: 1rem 
}
.nav ul {
    list-style: none;
    margin: 18px 0 0;
    padding: 0;
    text-align: center 
}
.nav ul li {
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin: 2px 10px 
}
.nav a {
    text-decoration: none;
    position: relative;
    padding-bottom: 1em;
    overflow: hidden 
}
.nav a, .nav a:hover {
    color: #fff 
}
.nav a:hover:after {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin-bottom: 4px;
    white-space: nowrap;
    content: attr(data-text) attr(data-text);
    color: transparent;
    line-height: 2;
    text-decoration: underline;
    -webkit-text-decoration-style: double;
    text-decoration-style: double;
    -webkit-text-decoration-color: #fff;
    text-decoration-color: #fff;
    animation: b 3s linear infinite 
}
@keyframes b {
    to {
        margin-left: -50.5% 
   }
}
nav a.active:after {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin-bottom: 4px;
    white-space: nowrap;
    content: attr(data-text) attr(data-text);
    color: transparent;
    line-height: 2;
    text-decoration: underline;
    -webkit-text-decoration-style: dotted;
    text-decoration-style: dotted;
    -webkit-text-decoration-color: #fff;
    text-decoration-color: #fff 
}
.main {
    position: relative;
    display: block;
    max-width: 100%;
    padding: 0rem 5rem;
}
.intro {
    margin-top: 2rem 
}
@media only screen and (max-width:48em) {
    .intro {
        margin-top: 0 
   }
}
.view-work {
    position: relative 
}
.view-work:after {
    content: "";
    position: absolute;
    top: .7rem;
    right: -1.6rem;
    width: 1rem;
    height: 24px;
    -khtml-transform: translateY(-50%);
    transform: translateY(-50%) 
}
.case-study {
    margin-top: 4rem 
}
.case-study ul {
    font-size: 2.8rem;
    list-style-type: none;
    padding: 0;
    margin: 0 
}
.case-study li {
    border-bottom: 1px dashed #2b2b2b;
    padding: 2rem 0;
    position: relative;
    display: block 
}
.case-study li:first-child {
    border-top: 1px dashed #2b2b2b 
}
.case-study li:after {
    content: "";
    position: absolute;
    margin: 2rem 0;
    right: 4px;
    width: 24px;
    height: 24px;
    transition: all .1s ease 
}
.case-study li:hover:after {
    right: 0 
}


.client-logo {
    margin-left: 4.2rem 
}
.client-logo:before {
    content: "";
    position: absolute;
    margin: 3rem 0;
    left: 0;
    width: 48px;
    height: 48px;
    transform: translateY(-50%) 
}



@media only screen and (max-width:64em) {
    .step {
        margin-top: 5rem;
        margin-bottom: 1rem 
   }
}
.step p {
    margin: 3rem 0 
}
.step h3 {
    font-size: 2.2rem;
    letter-spacing: 1px;
    position: relative;
    margin-bottom: 4rem 
}
.step h3:after {
    content: "";
    position: absolute;
    margin: 4.5rem 0;
    left: 0;
    width: 90px;
    height: 16px;
    transform: translateY(-50%) 
}
h1, h2, h3 {
    font-family: 'Fira Code', Times, serif;
    font-weight: 500 
}
*, h1, h2, h3 {
    color: #ffffff 
}
* {
    font-family: 'Fira Code', Courier, Helvetica, Arial, sans-serif;
    line-height: 1.5 
}
h1 {
    font-size: 4.2rem;
    line-height: 1;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    letter-spacing: 1px 
}
h1 br {
    line-height: 1rem 
}
h2 {
    font-size: 3rem;
    margin: 2rem 0 
}
.my-stats h2, h3 {
    font-size: 1.8rem;
    letter-spacing: 1px;
    position: relative 
}
h4 {
    font-size: 1.125rem 
}
h5 {
    font-size: 1rem 
}
h6 {
    font-size: .875rem 
}
p {
    color: #e9e9e9;
    font-size: .95rem;
    font-weight: 400;
    line-height: 1.8;
    margin-bottom: 1.8rem 
}
ul {
    list-style: none;
    padding: 0 
}
li {
    line-height: 2 
}
.text-link {
    display: inline-block 
}
a {
    text-decoration: none;
    color: #fff;
    transition: all .2s ease 
}
a:hover {
    color: #e4ebf4 
}
.font-light {
    font-weight: 300 
}
.font-regular {
    font-weight: 400 
}
.font-heavy {
    font-weight: 700 
}
.center {
    text-align: center 
}
.site *, .site h1, .site h2, .site h3 {
    color: #fff 
}
.site p {
    color: #ccc 
}
.web {
    color: #a0be8d 
}
.quote {
    max-width: 28.125rem;
    padding-right: 1rem 
}
@media print {
    * {
        background: transparent!important;
        color: #000!important;
        box-shadow: none!important;
        text-shadow: none!important 
   }
    a, a:visited {
        text-decoration: underline 
   }
    a[href]:after {
        content: " (" attr(href) ")" 
   }
    abbr[title]:after {
        content: " (" attr(title) ")" 
   }
    .ir a:after, a[href^="#"]:after, a[href^="javascript:"]:after {
        content: "" 
   }
    blockquote, pre {
        border: 1px solid #999;
        page-break-inside: avoid 
   }
    thead {
        display: table-header-group 
   }
    img, tr {
        page-break-inside: avoid 
   }
    img {
        max-width: 100%!important 
   }
    @page {
        margin: .5cm 
   }
    h2, h3, p {
        orphans: 3;
        widows: 3 
   }
    h2, h3 {
        page-break-after: avoid 
   }
}
.model-container {
    margin: 0 auto;
    padding: 0 5rem;
}
.footer-row {
    position: relative !important;
    top: 90vh;
    padding: 3rem;
    margin: 0 auto;
}
